@font-face {
  font-family: "Schwiffy";
  src: url(/ricknmorty/static/media/get_schwifty.4b4420cf.ttf);
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
  "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #1b7272;
  color: #cfcdcd;
  letter-spacing: 0.05em;
}

ul {
  list-style: none;
}

input, select {
  background: #cfcbcb;
}
.header {
  position: fixed;
  left: 0;
  right: 0;
  min-height: 53px;
  padding: 10px 0;
  background-color: #168a99;
  box-shadow: 1px -3px 3px 3px rgba(0, 0, 0, 0.54);
  z-index: 999; }

.header + div {
  padding-top: 80px; }

.nav-list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  list-style: none;
  padding-right: 50px; }

.nav-list-item {
  font-family: "Schwiffy";
  margin: 0 15px; }

.nav-list-item:hover a {
  color: #f9aa33; }

.nav-list-item a {
  font-size: 30px;
  color: #fff;
  border-bottom: 2px solid transparent;
  text-decoration: none;
  -webkit-transition: color .2s ease-in-out;
  transition: color .2s ease-in-out; }

.nav-list-item a.is-active {
  padding-bottom: 5px;
  color: #f9aa33;
  border-color: #f9aa33; }

.home-container {
  height: calc(100vh - 83px);
  background: url(/ricknmorty/static/media/rick-and-morty-wallpaper-portal-wallpaper-013.b90bffc4.jpg) no-repeat center 50px;
  background-size: cover; }

.home-title {
  text-align: left;
  margin: 30px auto auto 40px; }

.home-title h1 {
  font-family: "Schwiffy";
  color: #168a99;
  max-width: 400px;
  font-size: 70px;
  -webkit-text-stroke: .05px #000000;
  text-shadow: 1px 1px 2px #ffffff; }

.filter-form {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 20px 0; }
  .filter-form .filter-search,
  .filter-form .filter-select,
  .filter-form .filter-submit {
    font-family: "Schwiffy"; }
  .filter-form .filter-search::-webkit-input-placeholder,
  .filter-form .filter-select {
    color: #757682; }
  .filter-form .filter-search::-moz-placeholder,
  .filter-form .filter-select {
    color: #757682; }
  .filter-form .filter-search::-ms-input-placeholder,
  .filter-form .filter-select {
    color: #757682; }
  .filter-form .filter-search::placeholder,
  .filter-form .filter-select {
    color: #757682; }

.filter-search {
  font-size: 16px;
  width: 250px;
  padding: 10px;
  margin: 10px 8px;
  font-weight: 700;
  border: 1px solid rgba(0, 0, 0, 0.12);
  border-radius: 15px;
  color: #000; }

.filter-select {
  font-size: 16px;
  padding: 10px 10px 10px 25px;
  margin: 10px 8px;
  border: 1px solid rgba(0, 0, 0, 0.12);
  border-radius: 15px; }

.filter-submit {
  width: 90px;
  min-height: 40px;
  font-weight: 700;
  margin: 10px 8px;
  border: 1px solid rgba(0, 0, 0, 0.12);
  border-radius: 15px;
  background: #f9aa33;
  color: #232f34;
  cursor: pointer; }

@media (max-width: 899px) {
  .filter-form {
    margin: 80px 0 20px 0; } }

@media (max-width: 516px) {
  .filter-form {
    margin: 120px 0 20px 0; } }

@media (max-width: 369px) {
  .filter-form {
    margin: 200px 0 20px 0; } }

@media (max-width: 311px) {
  .filter-form {
    margin: 250px 0 20px 0; } }

.modal {
  width: 100vw;
  height: 100vh;
  background: #1b7272;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center; }
  .modal__content {
    display: flex;
    align-items: center;
    justify-content: space-around;
    text-align: center;
    box-sizing: border-box;
    padding: 15px;
    width: 900px;
    height: 500px;
    border-radius: 30px 30px 30px;
    background: #174c60; }
  .modal__left, .modal__right {
    padding: 20px;
    flex-grow: 1; }
  .modal__name {
    margin: 0 auto 20px; }
  .modal__image {
    border-radius: 10px; }
  .modal__close-button {
    font-family: "Schwiffy";
    width: 90px;
    min-height: 40px;
    font-weight: 700;
    margin: 10px 8px;
    border: 1px solid rgba(0, 0, 0, 0.12);
    border-radius: 15px;
    background: #f9aa33;
    color: #232f34;
    cursor: pointer;
    -webkit-transition: 0.3s ease;
    transition: 0.3s ease; }
    .modal__close-button:hover {
      background-color: #f94a33; }

.popup {
  cursor: pointer; }

@media (max-width: 899px) {
  .modal__content {
    width: 600px;
    height: 300px; }
  .modal__image {
    width: 150px;
    height: 150px; } }

@media (max-width: 369px) {
  .modal {
    align-items: flex-end; }
    .modal__content {
      height: 440px; } }

.card {
  margin: 10px;
  padding-bottom: 15px;
  width: 250px;
  color: #f9aa33;
  background-color: #174c60;
  border: 1px solid rgba(0, 0, 0, 0.12);
  border-radius: 5px;
  box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.12); }

.card-image {
  display: inline-block;
  width: 100%;
  border-radius: 4px 4px 0 0; }

.card-name {
  margin: 10px 0 0 15px;
  font-size: 1rem; }

.card-species, .card-gender {
  margin: 0 0 0 15px;
  color: #cfcbcb;
  font-weight: 300; }

.pagination {
  display: flex;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  justify-content: center;
  margin: 0 auto 20px;
  padding: 0;
  list-style: none; }

.pagination-item {
  font-family: "Schwiffy";
  padding: 5px 10px;
  border: 1px solid rgba(0, 0, 0, 0.12);
  border-right: none;
  color: #232f34;
  background: #886322;
  cursor: pointer; }

.pagination-item.active {
  background: #dd992e;
  color: #fff; }

.pagination-item:last-child {
  border-right: 1px solid rgba(0, 0, 0, 0.12); }

.preloader {
  margin-top: 5%;
  text-align: center; }

.characters {
  display: flex;
  max-width: 1600px;
  flex-wrap: wrap;
  justify-content: center;
  padding: 15px;
  overflow: auto; }

h1 {
  margin: 0; }

.locations,
.episodes {
  display: flex;
  max-width: 900px;
  margin: 0 auto 30px;
  justify-content: center;
  flex-direction: column;
  flex-wrap: wrap; }

.locations-table,
.locations-thead,
.locations-tbody,
.episodes-table,
.episodes-thead,
.episodes-tbody,
tr, td, th {
  color: #b8b8b8;
  border: 1px solid #242424; }

th, td {
  padding: 5px; }

.watchlist-form {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 30px; }
  .watchlist-form__error {
    display: block;
    text-align: center;
    color: #ef9292; }

.watchlist-item {
  display: flex;
  justify-content: center;
  align-items: center; }
  .watchlist-item__text {
    font-family: "Schwiffy";
    font-size: 16px;
    width: 500px;
    padding: 10px;
    margin: 10px 8px;
    font-weight: 700;
    border: 1px solid rgba(0, 0, 0, 0.12);
    border-radius: 15px;
    color: #232f34; }
  .watchlist-item__delete-button {
    width: 40px;
    min-height: 30px;
    font-weight: 700;
    margin: 10px 8px;
    border: 1px solid rgba(0, 0, 0, 0.12);
    border-radius: 10px;
    background: #f94a33;
    color: #232f34;
    cursor: pointer; }
  .watchlist-item__checkbox {
    width: 25px;
    min-height: 25px;
    font-weight: 700;
    border: 1px solid rgba(0, 0, 0, 0.12);
    background: #f94a33;
    color: #232f34;
    cursor: pointer; }

.filter-search {
  font-family: "Schwiffy"; }

